.hamburger-link {
    min-width: 40px;
}

.toggler {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
    width: 100%;
    height: 40px;
    opacity: 0;
}

/* Hamburger Line */
.hamburger {
    width: 100%;
    padding-top: 20px; 
    box-shadow: 0px 0px 10px 3px #000000;
}

.hamburger > div {
    position: relative;
    flex: none;
    width: 100%;
    height: 2px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease;
}
  
/* Hamburger Lines - Top & Bottom */
.hamburger > div::before,
.hamburger > div::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: -12px;
    width: 100%;
    height: 2px;
    right: 0;
    background: inherit;
    transition: all 0.4s ease; 
    box-shadow: 0px 0px 10px 3px #000000;
}

/* Moves Line Down */
.hamburger > div::after {
    top: 12px;
    width: 100%;
}

/* Turns Lines Into X */
.toggler:checked + .hamburger > div:before,
.toggler:checked + .hamburger > div:after {
    top: 0;
    transform: rotate(90deg);
    width: 100%;
}

.toggler:checked + .hamburger > div {
    transform: rotate(135deg);
}

.toggler:hover + .hamburger > div:before,
.toggler:hover + .hamburger > div:after  {
    width: 100%;
}

.toggler:checked:hover + .hamburger > div {
    transform: rotate(225deg);
}

