.alt-color-one::before {
  content: '';
  color: #4ECDC4;
  width: 40px;
  height: 40px;
  position: absolute;
  background: #4ECDC4;
  border-radius: 50%;
  opacity: 0.2;
  top: 0px;
  left: 0px;
  box-shadow: 120px 0px white;
  -webkit-animation: circle-move 6s;
          animation: circle-move 6s;
}

.alt-color-two::before {
  content: '';
  position: absolute;
  background: white;
  border-radius: 50%;
  opacity: 0.2;
  top: 0px;
  left: 0px;
  color: white;
  width: 30px;
  height: 30px;
  box-shadow: 120px 0px #e5166D;
  -webkit-animation: circle-move-two 5s;
          animation: circle-move-two 5s;
}

@-webkit-keyframes circle-move-two {
  0% {
    top: 50px;
    left: 0px;
    box-shadow: 120px -100px #e5166D;
  }
  100% {
    top: -50px;
    left: 100px;
    box-shadow: 120px 100px #e5166D;

  }
}

@keyframes circle-move-two {
  0% {
    top: 50px;
    left: 0px;
    box-shadow: 120px -100px #e5166D;
  }
  100% {
    top: -50px;
    left: 100px;
    box-shadow: 120px 100px #e5166D;

  }
}

@-webkit-keyframes circle-move {
  0% {
    top: -50px;
    left: 0px;
    box-shadow: 120px 100px white;

  }
  100% {
    top: 50px;
    left: 100px;
    box-shadow: 120px -100px white;

  }
}

@keyframes circle-move {
  0% {
    top: -50px;
    left: 0px;
    box-shadow: 120px 100px white;

  }
  100% {
    top: 50px;
    left: 100px;
    box-shadow: 120px -100px white;

  }
}

.hero-banner h2.toggle-true {
