.animated-bar {
  background-color: #e5166D;
  height: 5px;
  width: 100px;
  margin: 0 auto;
  -webkit-animation: swing 2s ;
          animation: swing 2s ;
}

@-webkit-keyframes swing {
  0% {
    transform: translate(-100px, 0px);
    width: 20px;
  }
  5% {
    transform: translate(-100px, 0px);
    width: 20px;
  }

  25% {
    width: 100px;
  }

  45% {
    transform: translate(100px, 0px);
    width: 20px;
  }  
  50% {
    transform: translate(100px, 0px);
    width: 20px;
  }
  55% {
    transform: translate(100px, 0px);
    width: 20px;
  }

  75% {
    width: 100px;
  }
}

@keyframes swing {
  0% {
    transform: translate(-100px, 0px);
    width: 20px;
  }
  5% {
    transform: translate(-100px, 0px);
    width: 20px;
  }

  25% {
    width: 100px;
  }

  45% {
    transform: translate(100px, 0px);
    width: 20px;
  }  
  50% {
    transform: translate(100px, 0px);
