html {
  font-size: 120%;
  background-color: #121216;
  color: white;
}

body {
  font-family: 'Poppins', sans-serif;  
  min-height: 100vh;

  background: linear-gradient(115deg, rgba(78, 205, 196, 0.05),rgba(229, 22, 109, 0.05));
  background-size: 400% 400%;

  -webkit-animation: AnimationName 15s ease infinite;
  animation: AnimationName 15s ease infinite;
}

body::before,
body::after {
  content: '';
  position: absolute;
  top: 20%;
  left: 10%;
  width: 500px;
  height: 500px;
  max-width: 90%;
  max-height: 80%;
  background-color: rgba(3,3,3,1);
  border-radius: 50%;
  opacity: 0.2;
}

body::before {
  -webkit-animation: bubbleOne 15s ease infinite;
          animation: bubbleOne 15s ease infinite;
}

body::after {
  z-index: -10;
  width: 490px;
  height: 520px;
  -webkit-animation: bubbleTwo 10s ease infinite;
          animation: bubbleTwo 10s ease infinite;

}

section {
  /* border: 10px solid salmon; */
  padding: 0 3rem;
}

