  100% { background-position:0% 50% }
}

.hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-bottom: 1px solid #e5166D;
}

.hero::after {
  content: '';
  width: 100%;
  border: 1px solid #e5166D;
  bottom: -1px;
  position: absolute;
  left: 0;
}

header {
  display: flex;
  justify-content: flex-end;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  opacity: 0.1;
}

main.main-body {
  max-width: 1600px;
  position: relative;
  margin: 0 auto;
  /* padding: 0 2rem; */
}

button {
  background: none;
  border: none;
  padding: 0;
  font-weight: inherit;
}

.foot-nav {
  display: none;
}

.foot-links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.foot-links .link {
  margin: 0;
  flex-grow: 1;
}

.foot-links .link a {
  display: block;
  padding: 1rem;
  text-align: center;
}

footer p {
  text-align: center;
  margin: 0;
  padding: 1rem;
}

@media only screen and (max-width: 600px) { 
  .foot-links .link {
    min-width: 50%;    
    background-color: #e5166D;
    opacity: 0.9;
  }
  .foot-links .link:nth-child(2),
  .foot-links .link:nth-child(3) {
    background-color: turquoise;
  }
  .foot-links .link:hover {
    opacity: 1;
  }

}


@media only screen and (max-width: 600px) {
  html {
    font-size: 100%;
  }
 
  /* main.main-body,  */
  section {
    padding: 0 5%;
  }

  .foot-nav {
    display: block;
  }
}
